div#myworksbeta {
  background-color: inherit;
  height: 90vh;
  padding: 8vh 5% 1vh 5%;
  width: 90%;
  overflow: auto;
  div.myworksbetabox {
    position: relative;
    padding-bottom: 1vh;
    div.carousel {
      position: relative;
      background-color: inherit;
      margin: 7vh 5vh 0 5vh;
      padding: 1vh 0;
      box-sizing: border-box;
      height: 70vh;
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      justify-content: space-around;

      div.maindisplay {
        background-color: inherit;
        height: 95%;
        width: 70%;
        div.sitenumber {
          position: absolute;
          top: -13%;
          right: 0;
          left: 0;
          margin: 0 auto;
          z-index: 100;
          width: 10%;
          height: 20%;
          overflow: hidden;

          div.slidecarrier {
            display: flex;
            flex-direction: row;
            font-size: 50px;
            justify-content: space-around;
            align-items: center;
            height: 100%;

            div.slider {
              padding: 10px 0;
              text-align: center;
              span {
                filter: drop-shadow(0 0 4px var(--primary-color));
              }
            }
          }
        }
        div.siteimage {
          background-color: var(--primary-color-lite);
          overflow: hidden;
          position: relative;

          width: 100%;
          height: 85%;
          z-index: 99;

          border-radius: 3vw;

          div.sitelinks {
            backdrop-filter: blur(5px) brightness(0.7);
            position: absolute;
            right: -1px;
            bottom: -1px;
            width: 10%;
            height: 35%;
            z-index: 101;

            border-radius: 3vw 0 0 0;

            display: flex;

            transition: backdrop-filter 500ms;

            div.linkicons {
              width: 100%;
              display: flex;
              flex-direction: column;
              justify-content: space-evenly;
              align-items: center;
              svg {
                width: 30px;
                padding: 5px;
                transition: 200ms;

                &:hover {
                  filter: drop-shadow(0 0 4px var(--primary-color));
                  transform: translateY(-3px) scale(1.1);
                }
              }
            }
          }

          div.slidecarrier {
            height: 100%;
            display: flex;
            flex-direction: row;

            filter: saturate(0.9) brightness(0.7);
            transition: filter 500ms;

            div.slider {
              margin: 0 0vw;
              flex-grow: 1;
              background-color: inherit;
              background-size: cover;
              height: 100%;
            }
          }

          &:hover {
            div.sitelinks {
              backdrop-filter: blur(0px) brightness(1);
            }
            div.slidecarrier {
              filter: saturate(1) brightness(1);
            }
          }
        }

        div.sitetext {
          position: absolute;
          bottom: 0;
          right: 0;
          left: 0;
          //background-color: var(--primary-color-lite);
          backdrop-filter: blur(5px) brightness(0.5);
          width: 40%;
          height: 25%;
          margin: 0 auto;
          overflow: hidden;
          z-index: 100;
          opacity: 1;
          transition: backdrop-filter 500ms;

          border-radius: 1vw;

          &.hidden {
            backdrop-filter: blur(0) brightness(1);
            opacity: 0.5;
          }

          div.slidecarrier {
            height: 100%;
            display: flex;
            flex-direction: row;

            div.slider {
              margin: 0 1vw;
              flex-grow: 1;
              background-color: inherit;

              height: 100%;

              h2 {
                margin-top: 10px;
                margin-bottom: 10px;
                &:nth-child(1) {
                  display: inline-block;
                  font-size: 20px;
                  text-decoration: none;
                }
                &:nth-child(2) {
                  display: inline-block;
                  font-size: 23px;
                  text-decoration: underline solid white 2px;
                  text-underline-offset: 2px;
                }
              }

              p {
                margin: 5px 0;
                font-size: 15px;
              }
            }
          }
        }
      }
    }

    div.pillar {
      position: relative;
      background-color: var(--primary-color-lite);
      height: 60%;
      width: 10%;
      border-radius: 10vw;
      margin-bottom: 2vh;
      border: 2px solid white;

      &.static {
        &.canNext:hover {
          div.bgBox {
            > svg {
              opacity: 0.5;
            }
          }
        }
        div {
          &.titleBox {
            height: 30px;
            padding-top: 50px;

            div {
              width: 100%;
              transition: width 500ms ease-in-out;
            }
          }
        }
      }

      div {
        position: absolute;
        right: 0;
        left: 0;
        border-radius: inherit;
        margin: 0 auto;
        overflow: hidden;
        &.titleBox {
          width: 150%;
          height: 30px;

          bottom: -50px;

          position: absolute;
          left: -25%;
          text-align: center;

          div {
            font-size: 20px;
            width: 0%;
            overflow: hidden;
            transition: width 500ms ease-in-out;
          }
        }

        &.numberBox {
          top: 5%;
          width: 70%;
          height: 17%;
          div.numberSlider {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            height: 500%;
            width: 100%;
            div.slider {
              position: relative;

              flex-basis: 1;
              flex-grow: 1;

              span {
                font-size: 30px;
                line-height: 50px;
                //transition: 200ms;
              }
            }
          }
        }
        &.bgBox {
          bottom: 5%;

          width: 80%;
          height: 70%;

          > svg {
            position: absolute;
            z-index: 101;
            width: 50%;
            top: 0;
            bottom: 0;
            right: 15px;
            margin: auto 0;
            opacity: 0;

            transition: opacity 200ms;

            .cls-1 {
              stroke-width: 20px;
            }
          }

          div.bgSlider {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            height: 500%;
            width: 100%;
            div.slider {
              width: 100%;
              background-size: cover;
              position: relative;
              flex-basis: 1;
              flex-grow: 1;
              &:not(.edge) {
                filter: blur(3px) brightness(1.3);
                transition: filter 200ms;
              }
              &.edge {
                background-color: var(--primary-color-lite);

                svg {
                  width: 100%;
                  height: 100%;
                }
              }
            }
          }
        }
      }

      &:hover {
        div {
          &.numberBox {
            div.numberSlider {
              div.slider:not(.edge) {
                span {
                  filter: drop-shadow(0 0 4px var(--primary-color));
                }
              }
            }
          }
          &.bgBox {
            div.bgSlider {
              div.slider:not(.edge) {
                filter: blur(0) brightness(1.3);
              }
            }
          }
        }
      }

      &.rightpillar {
        div.bgBox {
          svg {
            transform: scaleX(-1);
            left: 15px;
          }
        }
      }
    }
    div.selector {
      background-color: inherit;
      height: 10vh;
      width: 25%;
      margin: 0 auto;
      overflow: hidden;

      div.slidecarrier {
        background-color: inherit;
        width: 90%;
        height: 70%;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        div.slider {
          background-color: inherit;
          width: 30px;
          height: 30px;
          margin: 0 1px;
          svg.logo {
            width: 30px;
            height: 30px;
            border-radius: 40%;
            .cls-1 {
              stroke-width: 20px;
            }
            &:hover {
              filter: drop-shadow(0 0 4px var(--primary-color));
            }
          }
        }
      }
    }
  }

  .tooltip {
    display: none;
  }
}
